import React from "react";
import { NavLink, useHistory } from "react-router-dom";
import Logo from '../logo.svg'
import styled from 'styled-components'
import { Button } from 'antd';
import  { useStores} from '../stores'
import {observer} from 'mobx-react';
import { message } from "antd";

const Header = styled.header`
background-color:#02101f;
display:flex;
padding:10px 100px;
align-items:center;  //
// justify-content:center; //
`;
const Img = styled.img`
height:30px;
width:30px
`

const StyleLink = styled(NavLink)`  //linkStyleLink
color:#fff;
margin-left:30px;
&.active {border-bottom:1px solid #fff}
`

const Login = styled.div`
margin-left:auto;
// &.btn{margin-left: 20px;}

`;
const StyledButton = styled(Button)`
margin-left: 10px;
`;
const Span = styled.span`
color:#fff;
`


const Component = observer(() => {   //Component
    const {UserStore,AuthStore}  = useStores()
    const history = useHistory()   //{}

    const handleLogin = () => {
        history.push('/login')  //
    }
    
    const handleRegister = () => {
        history.push('/register')
    }
  
    const handleLogout = () => {
        AuthStore.logout()
        //history.push('/register')
        message.info('')
    }




    return (
        <Header>
            <Img src={Logo} />
            <nav>
                <StyleLink to='/' activeClassName="active" exact></StyleLink>
                <StyleLink to='/history' activeClassName="active"></StyleLink>
                <StyleLink to='/about' activeClassName="active"></StyleLink>
            </nav>
            <Login>
                {
            UserStore.currentUser ? <>    
           <Span> {UserStore.currentUser.attributes.username} </Span> <StyledButton type="primary" onClick={handleLogout}></StyledButton>
          </> : <>
          <StyledButton type="primary" onClick={handleLogin}></StyledButton>
          <StyledButton type="primary" onClick={handleRegister}></StyledButton>
          </>
                }
            </Login>
        </Header>
    );
})
export default Component;